.container {
    font-family: 'Source Sans Pro', sans-serif;
}

.h4 {
    margin-bottom: 24px;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    color: #1D1E26;
    flex: none;
    order: 0;
    flex-grow: 0;
}

.instructions {
    width: 270px;
    height: 48px;
    left: 1px;
    top: 0px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #303240;
}

.form-control:focus {
    outline: none !important;
    border: 1.5px solid #01A7F0;
}

p {
    font-weight: 600;
    line-height: 24px;
    font-size: 16px;
}

.p-2 {
    padding: 0px !important;
    width: 100%;
    height: 24px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #474A59;
    flex: none;
    order: 0;
    flex-grow: 0;
}

.p-3 {
    padding: 0px !important;
    height: 24px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
}

a {
    text-decoration: none;
    width: 46px;
    height: 24px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #008ACE;
}

.label {
    width: 220px;
    height: 18px;
    margin-bottom: 16px;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: #6C6F80;
    flex: none;
    order: 0;
    flex-grow: 0;
}

.label-1 {
    width: 220px;
    height: 18px;
    margin-bottom: 16px;
    margin-left: 180px;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: #6C6F80;
    flex: none;
    order: 0;
    flex-grow: 0;
}

.logo-icon {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 341px;
    height: 48px;
    flex: none;
    order: 0;
    align-self: stretch;
    flex-grow: 0;
    margin-left: -36px;
    margin-top: -43px;
    margin-bottom: 44px;
}

.verify-title {
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    color: #1D1E26;
    margin: auto;
}

.verify-text {
    margin-top: 9px;
    margin-bottom: 21px;
    width: 253px;
    height: 96px;
    left: 30.5px;
    top: 39px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #000000;
}

.click-link {
    width: 68px;
    height: 24px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #008ACE;
}

.link-wrapper {
    width: 110%;
    display: flex;
    grid-gap: 5px;
    gap: 5px;
}
.check-mail{
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #000000;

}

